.advantages {
	background-repeat: no-repeat;
	background-image: url(assets/img/bg.svg);
	background-position-y: 170px;
	max-width: 1440px;
	margin: 0 auto;
	.container {
		display: flex;
		flex-direction: column;
		align-items: center;
		.title-second {
			color: white;
		}
	}
	.advantages-content {
		display: flex;
		margin-bottom: 45px;

		&__box {
			position: relative;
			display: inline-block;

			.text-img {
				max-width: 337px;
				position: absolute;
				top: 0;
				left: 15%;
				width: 100%;
				height: 100%;
				display: flex;
				flex-direction: column;
				justify-content: center;
				align-items: center;
			}
			.title-second {
				margin-left: -50px;
			}

			.template-discription {
				text-align: start;
				max-width: 324px;
				color: white;
			}
		}
		&__box:first-child {
			margin-right: -83px;
		}
		&__box:last-child {
			margin-left: -83px;
		}
	}
}
