header {
	background-color: $bg-color;
	background-image: url(assets/img/noise.svg);
	.container {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}
	.logo {
		padding: 20px 0;
		margin-bottom: 8px;
		border-bottom: 3px solid $primary-color;
		border-bottom-left-radius: 3px;
		border-bottom-right-radius: 3px;
		max-width: 1376px;
		width: 100%;
		text-align: center;
		position: relative;
		&::before {
			content: "";
			position: absolute;
			left: 0%;
			top: 112%;
			transform: translateY(-55%);
			max-width: 1376px;
			width: 100%;
			height: 2px;

			background-color: $line-color;
			border-radius: 50px;
		}
		.navbar-brand {
			font-family: "Lobster";
			font-style: normal;
			font-weight: 400;
			font-size: 32px;
			line-height: 40px;
			letter-spacing: 0.04em;
			color: $primary-color;
			position: relative;
			&:before {
				content: "";
				position: absolute;
				left: -55%;
				top: 50%;
				transform: translateY(-55%);
				width: 2.6em;
				height: 2px;
				border-radius: 50px;
				background-color: $line-color;
			}
			&:after {
				content: "";
				position: absolute;
				left: 110%;
				top: 50%;
				transform: translateY(-55%);
				width: 2.6em;
				height: 2px;
				border-radius: 50px;
				background-color: $line-color;
			}
		}
	}
	.navbar-nav {
		display: flex;
		justify-content: center;
		align-items: center;
		margin: 0 auto;
	}
	.nav-item:last-child {
		.nav-link {
			margin-right: 0;
		}
	}
	.nav-link {
		padding: 20px 0;
		font-weight: 400;
		font-size: 16px;
		font-family: "Playfair Display";
		font-style: normal;
		font-weight: 500;
		font-size: 24px;
		line-height: 32px;
		margin-right: 110px;

		color: $button-main-color;

		&:hover {
			color: $stroke-color;
			transition: 0.5s;
		}
		&:focus {
			color: $button-main-color;
			transition: 0.5s;
		}
		&:active {
			color: $primary-color;
			transition: 0.5s;
		}
	}

	.navbar {
		padding: 0;
		margin-top: 8px;
		max-width: 1376px;
		width: 100%;

		border-bottom: 2px solid $line-color;

		margin-bottom: 24px;
		border-radius: 2px;
	}
	.container-fluid {
		display: flex;
		flex-direction: column;
		padding: 0;
	}

	.header-logo {
		display: inline-block;
	}
}
