@media screen and (max-width: 991px) {
	.btn.btn-primary {
		font-family: "Proza Libre";
		font-weight: 600;
		font-size: 20px;
		line-height: 27px;
		color: white;
		padding: 16px 60px;
		background: #e88360;
		border: 1px solid #d14614;
		border-radius: 16px;
	}
	.title-second {
		margin-bottom: 16px;
	}
	.container {
		padding: 0 16px;
	}
	.template-title {
		margin-top: 60px;
		margin-bottom: 32px;
		font-family: "Playfair Display";
		font-style: normal;
		font-weight: 700;
		font-size: 32px;
		line-height: 43px;
		color: #6f5643;

		padding: 8px 0;
		border: 2px solid $line-color;
		border-radius: 2px;
		border-right: none;
		border-left: none;
		position: relative;
		&::before {
			content: "";
			position: absolute;
			left: 0%;
			top: -10%;
			transform: translateY(-55%);
			max-width: 1376px;
			width: 100%;
			height: 3px;
			background-color: $primary-color;
			border-radius: 50px;
		}
	}
	.title-second {
		font-weight: 400;
		font-size: 16px;
		line-height: 22px;
	}
	.template-discription {
		font-family: "Proza Libre";
		font-weight: 400;
		font-size: 16px;
		line-height: 22px;
	}
	header {
		.container {
			padding: 0 16px;
			.container-fluid {
				flex-direction: row;
			}
			.logo {
				max-width: 190px;
				margin: 0 auto;

				border: none;
				&::before {
					display: none;
				}
				.navbar-brand {
					margin: 0;
					padding: 0;
					font-size: 24px;
					line-height: 30px;
					&:before {
						width: 1.2em;
						left: -25%;
					}
					&:after {
						width: 1.2em;
						left: 103%;
					}
				}
			}
			.navbar {
				margin: 0 auto 20px;
				border: none;

				&::after {
					content: "";
					position: absolute;
					left: 0%;
					top: 73px;
					transform: translateY(-55%);
					max-width: 1376px;
					width: 100%;
					height: 1px;

					background-color: $line-color;
					border-radius: 50px;
				}
				&::before {
					content: "";
					position: absolute;
					left: 0%;
					top: 68px;
					transform: translateY(-55%);
					max-width: 1376px;
					width: 100%;
					height: 2px;
					background-color: $primary-color;
					border-radius: 50px;
				}
				.nav-link {
					margin: 0;
				}
				.navbar-nav {
					margin-top: 30px;
				}
			}
		}
		.navbar-toggler {
			width: 44px;
			height: 44px;
			position: relative;
			background-image: url("data:image/svg+xml,%3Csvg width='44' height='44' viewBox='0 0 44 44' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect x='0.5' y='0.5' width='43' height='43' rx='21.5' fill='%23E1B971'/%3E%3Crect x='0.5' y='0.5' width='43' height='43' rx='21.5' stroke='%236F5643'/%3E%3C/svg%3E");
			background-position: center;
			background-repeat: no-repeat;
			&:focus {
				box-shadow: none;
			}

			span {
				cursor: pointer;
				border-radius: 1px;
				height: 2px;
				width: 24px;
				background: #6f5643;
				position: absolute;
				left: 20%;
				top: 50%;
				display: block;
				content: "";
			}

			span:before,
			span:after {
				cursor: pointer;
				border-radius: 1px;
				height: 2px;
				width: 24px;
				background: #6f5643;
				position: absolute;
				left: 0%;
				top: 50%;
				display: block;
				content: "";
			}

			span:before {
				top: -5px;
			}

			span:after {
				top: 5px;
			}

			span,
			span:before,
			span:after {
				transition: all 0.5s ease-in-out;
			}
			&[aria-expanded="true"] {
				span {
					background-color: transparent;
				}
				span:before,
				span:after {
					top: 0;
				}
				span:before {
					transform: rotate(135deg);
				}
				span:after {
					transform: rotate(-135deg);
				}
			}
		}
	}

	.first-screen {
		.first-title {
			font-style: normal;
			font-weight: 400;
			font-size: 20px;
			line-height: 25px;
			color: #8a7768;
			padding: 0 16px;
		}
	}
	.about {
		background-position-y: 70px;
		padding-bottom: 20px;
		position: relative;
		background-size: cover;
		.about-template {
			flex-direction: column;
			align-items: center;

			.about-img {
				position: absolute;
				max-width: 158px;
				top: 60%;

				left: 65%;
			}
			.about-box {
				margin: 0;
				.about-box__item {
					margin: 22px auto 40px;
					padding: 24px 16px;
					max-width: 400px;
				}
			}
			.btn-box {
				max-width: 263px;
			}
			.btn.btn-primary {
				font-family: "Proza Libre";
				font-weight: 600;
				font-size: 20px;
				line-height: 27px;
				color: white;
				padding: 16px 60px;
				background: #e88360;
				border: 1px solid #d14614;
				border-radius: 16px;
			}
		}
	}
	.slider {
		margin-top: 10px;
		.main-box-first {
			display: flex;
			flex-direction: column;
			margin-bottom: 32px;
		}
		.box-main-second {
			display: flex;
			flex-direction: column;

			.about_box {
				order: 2;

				.about-box__item {
					max-width: 352px;
					padding: 24px 16px;
					margin: 0;
					transform: translate(0px, -5px);
				}
			}
		}
		.slider-template {
			max-width: 358px;
			margin: 0 auto 11px;
		}
		.slider-first-img {
			max-width: 358px;
		}
		.slider-second-img {
			width: 108.67px;
			height: 88px;
			border-radius: 16px;
			border: 2px solid #6f5643;
		}
		.slider-template-second {
			margin: 0 auto 24px;
			max-width: 358px;
			position: relative;
			margin-left: 0;
			order: 1;
		}
		.slider-template__item-wrapper-second {
			margin: 0 16px;
		}
		.slick-arrow {
			width: 44px;
			height: 44px;
			left: 204px;
			top: -80px;
			position: absolute;
			&.slick-next {
				background-image: url("data:image/svg+xml,%3Csvg width='72' height='68' viewBox='0 0 72 68' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect width='68' height='68' rx='34' fill='%236F5643'/%3E%3Crect x='0.5' y='0.5' width='67' height='67' rx='33.5' fill='%23E1B971'/%3E%3Cpath d='M29.9113 51.8511L46.2177 35.45C46.4113 35.2548 46.5487 35.0432 46.63 34.8154C46.7113 34.5877 46.7513 34.3436 46.75 34.0833C46.75 33.8229 46.71 33.5789 46.63 33.3511C46.55 33.1233 46.4126 32.9117 46.2177 32.7165L29.9113 16.2666C29.4597 15.811 28.8952 15.5833 28.2177 15.5833C27.5403 15.5833 26.9597 15.8273 26.4758 16.3154C25.9919 16.8036 25.75 17.373 25.75 18.0239C25.75 18.6747 25.9919 19.2442 26.4758 19.7323L40.7016 34.0833L26.4758 48.4342C26.0242 48.8898 25.7984 49.4514 25.7984 50.1192C25.7984 50.7869 26.0403 51.3642 26.5242 51.8511C27.0081 52.3392 27.5726 52.5833 28.2177 52.5833C28.8629 52.5833 29.4274 52.3392 29.9113 51.8511Z' fill='%236F5643'/%3E%3Crect x='0.5' y='0.5' width='67' height='67' rx='33.5' stroke='%236F5643'/%3E%3C/svg%3E");
				background-position: center;
				background-repeat: no-repeat;
				background-size: contain;
				&:hover {
					width: 44px;
				}
			}
			&.slick-prev {
				background-image: url("data:image/svg+xml,%3Csvg width='72' height='68' viewBox='0 0 72 68' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect width='68' height='68' rx='34' transform='matrix(-1 0 0 1 72 0)' fill='%236F5643'/%3E%3Crect x='-0.5' y='0.5' width='67' height='67' rx='33.5' transform='matrix(-1 0 0 1 71 0)' fill='%23E1B971'/%3E%3Cpath d='M42.0887 51.8511L25.7823 35.45C25.5887 35.2548 25.4513 35.0432 25.37 34.8154C25.2887 34.5877 25.2487 34.3436 25.25 34.0833C25.25 33.8229 25.29 33.5789 25.37 33.3511C25.45 33.1233 25.5874 32.9117 25.7823 32.7165L42.0887 16.2666C42.5403 15.811 43.1048 15.5833 43.7823 15.5833C44.4597 15.5833 45.0403 15.8273 45.5242 16.3154C46.0081 16.8036 46.25 17.373 46.25 18.0239C46.25 18.6747 46.0081 19.2442 45.5242 19.7323L31.2984 34.0833L45.5242 48.4342C45.9758 48.8898 46.2016 49.4514 46.2016 50.1192C46.2016 50.7869 45.9597 51.3642 45.4758 51.8511C44.9919 52.3392 44.4274 52.5833 43.7823 52.5833C43.1371 52.5833 42.5726 52.3392 42.0887 51.8511Z' fill='%236F5643'/%3E%3Crect x='-0.5' y='0.5' width='67' height='67' rx='33.5' transform='matrix(-1 0 0 1 71 0)' stroke='%236F5643'/%3E%3C/svg%3E%0A");
				width: 44px;

				left: 112px;
				top: -80px;
			}
		}
	}
	.advantages {
		background-size: cover;

		.advantages-content {
			flex-direction: column;

			&__box:first-child {
				margin-right: 0px;
				margin-bottom: -108px;
			}
			&__box:last-child {
				margin-left: 0px;
				margin-top: -108px;
			}

			.text-img {
				max-width: 338px;
			}
		}
		.title-second {
			margin-left: 0px;
		}
		.template-discription {
			max-width: 340px;
		}
		.title-second.main-advantages {
			margin: 0 auto;
			font-family: "Lobster";
			font-style: normal;
			font-weight: 400;
			font-size: 24px;
			line-height: 30px;
			text-align: center;
			color: #8a7768;
		}
	}
	.menu {
		.menu-mainbox {
			flex-direction: column;
			justify-content: center;
			padding: 0;
			border: none;
			.margen-menu {
				margin-top: 0;
			}
			&__template {
				padding: 10px;
				min-width: 200px;
				border: 1px solid #6f5643;
				margin-bottom: 18px;
				&-name {
					font-size: 24px;
					line-height: 30px;
				}
				&-item {
					border-bottom: 1px solid #b19e90;
				}
			}
			.menu-fast {
				margin-left: 0;
			}
			&__template-item {
				.group-menu {
					font-weight: 400;
					font-size: 20px;
					line-height: 25px;
				}
				&-description {
					font-weight: 400;
					font-size: 16px;
					line-height: 22px;
				}
			}
		}
	}
}
