footer {
	background-color: $element-color;
	padding: 45px 0 20px;
	.container {
		.navbar-nav-footer {
			padding: 0;
		}
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		.nav-item:last-child {
			.nav-link {
				margin-right: 0;
			}
		}
		.navbar-nav-footer {
			display: flex;
			justify-content: center;
			align-items: center;
			list-style: none;
			margin: 28px 0 25px;
			.nav-link {
				font-family: "Playfair Display";
				font-style: normal;
				font-weight: 500;
				font-size: 24px;
				line-height: 32px;
				margin-right: 45px;

				color: #ffffff;

				&:hover {
					color: #8a7768;
					transition: 0.5s;
				}
				&:focus {
					color: $button-main-color;
					transition: 0.5s;
				}
				&:active {
					color: #6f5643;
					transition: 0.5s;
				}
			}
		}
	}
