		.icon-footer:hover {
			transition: 0.4s;
			stroke: #ffffff;
			rect {
				fill: #8a776842;
			}

			path {
				fill: white;
			}
		}
		.icon-footer:active {
			transition: 0.4s;
			stroke: #ffffff;
			rect {
				fill: #6f56435a;
			}

