	.navbar-brand {
		font-family: "Lobster";
		font-style: normal;
		font-weight: 400;
		font-size: 32px;
		line-height: 40px;
		/* identical to box height */
		letter-spacing: 0.04em;
		color: #ffffff;
		position: relative;
		&:before {
			content: "";
			position: absolute;
			left: -55%;
			top: 50%;
			transform: translateY(-55%);
			width: 80px;
			height: 2px;
			background-color: $stroke-color;
			border-radius: 16px;
		}
		&:after {
			content: "";
			position: absolute;
			left: 110%;
			top: 50%;
			transform: translateY(-55%);
			width: 80px;
			height: 2px;
			background-color: $stroke-color;
			border-radius: 16px;
		}
	}
}
